.mediatk-settings-page {
  --mediatk-brand-color: #2764f1;
  --mediatk-danger-color: #47d87c;
}

@keyframes mediaTkLoadingSpinner {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(359deg);
  }
}

.mediatk-settings-page .button.is-loading {
  pointer-events: none;
  justify-content: center;
  align-items: center;
  display: inline-flex;
  color: #0000 !important;
}

.mediatk-settings-page .button.is-loading:after {
  content: "";
  border: 2px solid #dbdbdb;
  border-radius: 290486px;
  width: 1em;
  height: 1em;
  animation: .5s linear infinite mediaTkLoadingSpinner;
  display: block;
  position: absolute;
  border-color: #0000 #0000 #fff #fff !important;
}

.mediatk-settings-page .button-link {
  background-color: #0000;
  border-width: 0;
  font-size: 17px;
  text-decoration: underline;
}

.mediatk-settings-page .button-link:focus, .mediatk-settings-page .button-link:active {
  box-shadow: none;
  border-width: 0;
  outline: none;
}

.mediatk-settings-page .button-link.is-disabled {
  color: #ddd;
}

.mediatk-settings-page .button-link.is-right-side {
  float: right;
}

.mediatk-settings-page .button-link.is-right-side.is-loading {
  justify-content: flex-end;
}

.mediatk-settings-page .button-link.is-loading {
  pointer-events: none;
  justify-content: center;
  align-items: center;
  display: inline-flex;
  color: #0000 !important;
}

.mediatk-settings-page .button-link.is-loading:after {
  content: "";
  border: 2px solid #7ab4e3;
  border-radius: 290486px;
  width: 1em;
  height: 1em;
  animation: .5s linear infinite mediaTkLoadingSpinner;
  display: block;
  position: absolute;
  border-color: #0000 #0000 #2271b1 #2271b1 !important;
}

.mediatk-settings-page .submission-status {
  margin-left: 15px;
  line-height: 39px;
}

.mediatk-settings-page .submission-status.is-hidden {
  display: none;
}

.mediatk-settings-page .submission-status.is-success {
  color: #0fa953;
}

.mediatk-settings-page .submission-status.is-error {
  color: tomato;
}

.mediatk-text-field {
  width: 100%;
  max-width: 250px;
}

.mediatk-text-field.is-fullwidth {
  max-width: 100%;
}

.mediatk-text-field.is-large {
  max-width: 400px;
}

.mediatk-dimension-field {
  width: 120px;
}

.mediatk-dimensions {
  align-items: center;
  display: flex;
}

.mediatk-dimensions .mediatk-dimension {
  width: 120px;
}

.mediatk-dimensions .mediatk-dimension:not(:last-child) {
  margin-right: 10px;
}

.mediatk-dimensions label {
  margin-bottom: 5px;
  display: block;
}

.mediatk-dimensions .mediatk-dimension-field {
  width: 100%;
}

.mediatk-featured-products > h2 {
  text-align: center;
  margin-bottom: 50px;
  font-size: 24px;
}

.mediatk-featured-products .products {
  justify-content: space-between;
  display: flex;
}

.mediatk-featured-products .products li {
  width: 32%;
}

.mediatk-featured-products .products .subheadline {
  margin-top: -6px;
  font-weight: 600;
}

.mediatk-featured-products .products img {
  max-width: 100%;
}

.mediatk-featured-products .credit {
  text-align: center;
  opacity: .5;
  margin-top: 20px;
}

.heatbox {
  position: relative;
}

.heatbox .checkbox-label .is-disabled {
  opacity: .6;
}

.heatbox .heatbox-overlay.is-hidden {
  display: none;
}

.heatbox .heatbox-overlay {
  z-index: 100;
  background-color: #0000001a;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.heatbox .form-table th .description {
  color: #646970;
  font-style: italic;
  font-weight: 400;
}

.heatbox .mediatk-checkbox-label .description {
  color: #646970;
  font-style: italic;
}

.heatbox .radio-label {
  padding-left: 28px;
}

.mediatk-multiline-radio-fields .radio-label {
  display: block;
}

.mediatk-multiline-radio-fields .radio-label:not(:last-child) {
  margin-bottom: 10px;
}

.mediatoolkit-settings-form .row-disabled {
  background-color: #f5f5f5;
}

